.Cat{
    width: 200px;
    height: 200px;
    background-color: #FFf;
    position: relative;
    border-radius: 50%;
    zoom:0.5;
}

@media only screen and (max-width: 768px) {
    .Cat {
        zoom:0.4;
    }
}

.LeftEye{
    width: 15px;
    height: 15px;
    background-color: #88B0E4;
    position: absolute;
    top: 30%;
    left: 25%;
    border-radius: 50%;
}

.RightEye{
    width: 15px;
    height: 15px;
    background-color: #88B0E4;
    position: absolute;
    top: 30%;
    left: 60%;
    border-radius: 50%;
}

.RightEar{
    width: 0;
	height: 0;
	border-left: 20px solid transparent;
	border-right: 20px solid transparent;
    border-bottom: 40px solid #FFF;
    position: absolute;
    top: -10%;
    left: 60%;
}

.LeftEar{
    width: 0;
	height: 0;
	border-left: 20px solid transparent;
	border-right: 20px solid transparent;
    border-bottom: 40px solid #FFF;
    position: absolute;
    top: -10%;
    left: 20%;
}

.Tail{
    width: 100px;
    height: 20px;
    background-color: #FFF;
    position: absolute;
    right: -40%;
    top: 60%;
    transform: rotate(-10deg);
}

.Tail::after{
    content: '';
    height: 20px;
    width: 20px;
    background-color: #FFF;
    position: absolute;
    right: -10px;
    border-radius: 50%;
}