.dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #88B0E4;
    display: inline-block;
    margin: 0 5px;
    border: solid #88B0E4 0.5px;
}

.blank-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #fff;
    border: solid #88B0E4 0.5px;
    display: inline-block;
    margin: 0 5px;
}