.App {
  background-color: #88B0E4;
  width: 100vw;
  height: 100vh;
  margin: 0px;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-family: 'Inconsolata', monospace;
  color: #88B0E4;
}

.container {
  width: 100%;
  max-width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.menu-container{
  flex: 1;
}

.tv {
  display: flex;
  flex-direction: row;
  border: solid 5px #fff;
  border-radius: 20px;
  height: 500px;
  overflow: hidden;
}

.left-container {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}

.right-container {
  display: flex;
  flex: 1;
  position: relative;
}

@media only screen and (max-width: 768px) {
  .tv{
    flex-direction: column-reverse;
  }
  .left-container {
    flex-direction: row;
  }
  }

  .copyright {
    position: absolute;
    text-align: center;
    bottom: 0px;
    color: #fff;
  }

  a{
    text-decoration: none;
  }
  
  a:visited{
    text-decoration: none;
  }

  a:hover {
    opacity: 0.5;
  }